import React from 'react'
import styled from 'styled-components'

function Membership() {
    return (
        <Container id='membership'>
            <Card>
                <Card1>
                    <h1><span>1 Month</span></h1>
                    <h1>250QR</h1>
                </Card1>
                <Card2>
                    <h1><span>3 Months</span></h1>
                    <h1>600QR</h1>
                </Card2>
                <Card3>
                    <h1><span>6 Month</span></h1>
                    <h1>850QR</h1>
                </Card3>
                <Card4>
                    <h1><span>1 Year</span></h1>
                    <h1>1500QR</h1>
                </Card4>
            </Card>
            <Text>
                <h1>Get Trained By <span>Professional Trainers</span></h1>
            </Text>
        </Container>
    )
}

export default Membership

const Container = styled.div`
    width:100%;
    height:100vh;
    background:url(https://img5.goodfon.com/wallpaper/nbig/f/15/back-fitness-gym-power-pose.jpg);
    background-position:center;
    background-size:cover;
    text-align:center;
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:center;
    color:white;
    @media only screen and (max-width:768px){
        
    }
`

const Card = styled.div`
    width:90%;
    display:flex;
    justify-content:space-around;
    h1{
        font-size:3rem;
    }
    span{
        color:#CE2A2A;
    }
    @media only screen and (max-width:768px) and (min-height:590px){
        h1{
            font-size:2rem;
        }   
        flex-direction:column;
    }
    @media screen and (max-width:768px) and (max-height:590px){
        display:flex;
        h1{
            font-size:2rem;
        }   
    }
`
const Card1 = styled.div`
    padding:10px;
    @media only screen and (max-width:768px){
        padding-bottom:25px;
    }
`
const Card2 = styled.div`
    padding:10px;
    @media only screen and (max-width:768px){
        padding-bottom:25px;
    }
`
const Card3 = styled.div`
    padding:10px;
    @media only screen and (max-width:768px){
        padding-bottom:25px;
    }
`
const Card4 = styled.div`
    padding:10px;
    @media only screen and (max-width:768px){
        padding-bottom:25px;
    }
`
const Text = styled.div`
    padding-top:80px;
    span{
        color:#CE2A2A;
    }
    @media screen and (max-width:768px) and (max-height:590px){
        padding-top:50px;
    }
`