import React from 'react'
import styled from 'styled-components'

function Home() {
    return (
        <Container id='home'>
            <Main>
                <h1><span>LIFESTYLE </span>GYM</h1>
                <h1>and <span>CROSS </span>FIT</h1>
                <p>FOR A HEALTHY LIFESTYLE</p>
            </Main>
        </Container>
    )
}

export default Home

const Container = styled.div`
    width:100%;
    height:100vh;
    background:url(https://images.unsplash.com/photo-1605296867304-46d5465a13f1?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=2700&q=80);
    background-position:center;
    background-size:cover;
    display:grid;
    place-items:center;
    color:white;
`
const Main = styled.div`
    text-align:center;
    h1{
        font-size:4rem;
    }
    span{
        color:#CE2A2A;
    }
    p{
        font-size:1.5rem;
    }
    @media only screen and (max-width:510px){
        h1{
            font-size:2.5rem;
        }
        span{
            color:#CE2A2A;
        }
        p{
            font-size:1rem;
        }
    }
`