import React from 'react'
import styled from 'styled-components'
import InstagramIcon from '@material-ui/icons/Instagram';
import WhatsAppIcon from '@material-ui/icons/WhatsApp';
import {Link, BrowserRouter as Router} from 'react-router-dom'

function Contact() {
    return (
        <Router>
            <Container id='contact'>
                <Content>
                    <Map>
                        <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d230484.92636062982!2d51.32141131389756!3d25.494139531542974!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3e45f9c51a516c5f%3A0x4a596c52e3086093!2sLife%20Style%20Gym%20W.L.L!5e0!3m2!1sen!2sqa!4v1614758894122!5m2!1sen!2sqa" width="600" height="450" style={{border:0}} allowfullscreen="" loading="lazy"></iframe>
                    </Map>
                    <ContactDetails>
                        <Address>
                            <h1>Address</h1>
                            <h3>1st Floor, Al Asmakh Building,</h3> 
                            <h3>Zone 74, Street 880, Building 106,</h3>
                            <h3>Al Khor Qatar</h3>
                        </Address>
                        <Time>
                            <h1>Timings</h1>
                            <h3>Sat - Thrus</h3>
                            <h3>24 Hours</h3>
                        </Time>
                        <Phone>
                            <h1>Contact</h1>
                            <h3>Tel: 44802332</h3>
                            <h3>Phone: 70025678</h3>
                            <h3>33456950</h3>
                            <Link to={{pathname:'whatsapp://send?phone=+97433456950&text='}}>
                                <WhatsAppIcon>WhatsApp</WhatsAppIcon>
                            </Link>
                        </Phone>
                        <Social>
                            <h1>Social</h1>
                            <Link to={{pathname:"https://www.instagram.com/lifestyle_gym_wll/"}}>
                                <InstagramIcon/>
                            </Link>
                        </Social>
                    </ContactDetails>
                </Content>
                <Footer>
                    <small>&copy;2021 Lifestyle Gym WLL</small>
                </Footer>
            </Container>
        </Router>

    )
}

export default Contact

const Container = styled.div`
    background:url(https://c4.wallpaperflare.com/wallpaper/37/985/194/workout-gym-bodybuilder-wallpaper-preview.jpg);
    background-position:center;
    background-size:cover;
    width:100%;
    height:100vh;
    display:flex;
    text-align:center;
    justify-content:center;
    flex-direction:column;
    color:white;
`
const Content = styled.div`
    width:100%;
    display:flex;
    justify-content:space-around;
    @media only screen and (max-width:768px) and (min-height:590px){
        flex-direction:column;
    }
`
const Map = styled.div`
    iframe{
        height:70vh;
    }
    @media only screen and (max-width:768px) and (min-height:590px){
        iframe{
            width:40vh;
            height:20vh;
        }
    }
    @media screen and (max-width:768px) and (max-height:590px){
        iframe{
            width:20vh;
            height:70vh;
        }
    }

`
const ContactDetails = styled.div`
    width:40%;
    display:flex;
    justify-content:space-around;
    flex-direction:column;
    align-items:center; 
    height:70vh;
    a{
        color:#25D366;
        text-decoration:none;
    }
    a:nth-child(2){
        color:#bc1888;
        text-decoration:none;
    }
    h1{
        font-size:2.5rem;
    }
    h3{
        font-size:1.2rem;
    }
    @media only screen and (max-width:768px) and (min-height:590px){
        width:100%;
        h1{
            font-size:2.1rem;
        }
        h3{
            font-size:1rem;
        }
    }
    @media screen and (max-width:768px) and (max-height:590px){
        width:70%;
        flex-direction:row;
        align-items:baseline;
        h1{
            font-size:2rem;
        }
        h3{
            font-size:1rem;
        }
    }
`
const Address = styled.div`
height:20vh;
display:flex;
flex-direction:column;
justify-content:space-evenly;
    h1{
        color:#CE2A2A;
    }
    @media screen and (max-width:768px) and (max-height:590px){
        padding-right:15px;
    }
`
const Time = styled.div`
height:20vh;
display:flex;
flex-direction:column;
justify-content:space-evenly;
    h1{
        color:#CE2A2A;
    }
    @media screen and (max-width:768px) and (max-height:590px){
        padding-right:15px;
    }
`
const Phone = styled.div`
height:20vh;
display:flex;
flex-direction:column;
align-items:center;
justify-content:space-evenly;
    h1{
        color:#CE2A2A;
    }
    @media screen and (max-width:768px) and (max-height:590px){
        padding-right:15px;
    }
`
const Social = styled.div`
height:20vh;
display:flex;
flex-direction:column;
justify-content:space-evenly;
    h1{
        color:#CE2A2A;
    }
`
const Footer = styled.div`
padding-top:20px;
align-items:center;
@media screen and (max-width:768px) and (max-height:590px){
    padding-top:0;
}
`